@charset "UTF-8";
.about-page {
  padding-top: 2rem;
  min-height: 95vh;
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  justify-content: center; }
  @media (max-width: 425px) {
    .about-page {
      padding-top: 3rem; } }
  .about-page h1 {
    padding: 1rem 0;
    font-size: 3rem; }
  .about-page .content-grid {
    flex: auto; }
  .about-page .about-wrapper .about-content {
    font-size: 0.875rem;
    line-height: 1.5rem; }
    @media (max-width: 425px) {
      .about-page .about-wrapper .about-content {
        font-size: 1rem; } }
    .about-page .about-wrapper .about-content blockquote {
      background: #f9f9f9;
      border-left: 10px solid #ccc;
      margin: 1.5em 10px;
      padding: 0.5em 10px;
      quotes: "“" "”" "‘" "’"; }
    .about-page .about-wrapper .about-content blockquote:before {
      color: #ccc;
      content: open-quote;
      font-size: 4em;
      line-height: 0.1em;
      margin-right: 0.25em;
      vertical-align: -0.4em; }
    .about-page .about-wrapper .about-content blockquote p {
      display: inline; }
    .about-page .about-wrapper .about-content p {
      font-weight: 300;
      padding: 0.5rem 0; }
    .about-page .about-wrapper .about-content .text-emoji {
      padding: 2rem 0;
      font-size: 1.5rem;
      font-weight: 300;
      text-align: center; }
